import {Mesh, Geometry, BoxGeometry, MeshBasicMaterial} from 'three';

import {Random} from './random';

export class Level extends Mesh {
	constructor() {
		var material = new MeshBasicMaterial({
			color: 0x00ff00
		});
		var cube = new BoxGeometry(1, 1, 1);
		super(cube, material);

		//this.setGeometry = this.buildGeometry();

		this.random = new Random(123);


	}

	buildGeometry() {
		var geometry = new THREE.Geometry();

		for (var i = 0; i < 10; i++) {

			geometry.vertices.push(
				new THREE.Vector3(this.random.inRange(0, 10), i, 0),
				new THREE.Vector3(0, i, 0),
				new THREE.Vector3(0, i - 1, 0),
				new THREE.Vector3(this.random.inRange(0, 10), i - 1, 0)
			);
			geometry.faces.push(new THREE.Face4(i * 4, i * 4 + 1, i * 4 + 2, i * 4 + 3));
		}


		geometry.computeBoundingSphere();

		return geometry;
	}
}


